html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

main {
  display: block; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

pre {
  font-family: monospace, monospace;
  font-size: 1em; }

a {
  background-color: transparent; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted; }

b,
strong {
  font-weight: bolder; }

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

img {
  border-style: none; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  padding: 0.35em 0.75em 0.625em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

progress {
  vertical-align: baseline; }

textarea {
  overflow: auto; }

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px; }

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit; }

details {
  display: block; }

summary {
  display: list-item; }

template {
  display: none; }

[hidden] {
  display: none; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  font-family: "Intro", "Arial", sans-serif;
  background-color: var(--color-white);
  display: flex;
  flex-direction: column;
  justify-content: center; }

img {
  max-width: 100%; }

a {
  text-decoration: none;
  color: #000; }
  a:visited {
    color: rgba(0, 0, 0, 0.8); }

.list-reset {
  list-style-type: none;
  padding: 0;
  margin: 0; }

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1210px; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0); }

.grid {
  display: grid;
  grid-auto-flow: column;
  gap: 1.25rem; }

ul, ol {
  list-style-type: none;
  padding: 0;
  margin: 0; }

.logotypes h2 {
  font-size: 2rem;
  text-transform: uppercase;
  color: slategray;
  text-align: center; }
  @media (min-width: 600px) {
    .logotypes h2 {
      font-size: 3rem;
      text-align: left; } }

.logotypes__content {
  position: relative;
  margin-bottom: 2.5rem; }
  .logotypes__content::after, .logotypes__content::before {
    content: "";
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: lightgray; }
  .logotypes__content::before {
    bottom: unset;
    top: 0; }

.logotypes__list {
  position: relative;
  display: grid;
  grid-template-columns: 1fr; }
  .logotypes__list--centered {
    justify-content: space-between; }
  @media (min-width: 400px) {
    .logotypes__list {
      grid-template-columns: 1fr 1fr; } }
  @media (min-width: 600px) {
    .logotypes__list {
      grid-template-columns: repeat(3, 1fr); } }
  @media (min-width: 700px) {
    .logotypes__list {
      grid-template-columns: auto;
      grid-auto-flow: column;
      gap: 20px; } }

.logotypes__item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.2;
  transition: 0.3s ease-in-out; }
  .logotypes__item svg {
    width: 90px;
    height: 90px; }
  .logotypes__item:hover {
    opacity: 1; }
  .logotypes__item:not(:last-child)::after {
    content: "";
    position: absolute;
    bottom: 0;
    width: 25px;
    height: 1px;
    background-color: gray; }
    @media (min-width: 400px) {
      .logotypes__item:not(:last-child)::after {
        bottom: unset;
        right: -10px;
        width: 1px;
        height: 25px; } }
  .logotypes__item--centered:not(:last-child)::after {
    display: none; }
